.blkcent {
  position: relative;
  width: 1200px;
  margin: 0 auto;
}
.about {
  margin-top: 76px;
  padding-top: 200px;
  padding-bottom: 100px;
}
.about > div {
  position: relative;
  box-sizing: border-box;
}
.about .left {
  float: left;
  width: 550px;
}
.about .left img {
  display: block;
  height: 50px;
}
.about .left h3 {
  font-size: 50px;
  color: #303030;
  line-height: 42px;
  margin-top: 30px;
  font-weight: 500;
}
.about .left p {
  color: #666666;
  font-size: 15px;
  margin-top: 30px;
  line-height: 31px;
}
.about .right {
  float: right;
  width: 500px;
}
.about .right h4 {
  font-size: 26px;
  color: #303030;
  margin-bottom: 35px;
  margin-top: 20px;
}
.about .right li {
  position: relative;
  margin-bottom: 15px;
  color: #4A4A4A;
  font-size: 15px;
}
.about .right li::after {
  content: "";
  display: block;
  position: absolute;
  left: -30px;
  top: 6px;
  width: 10px;
  height: 5px;
  border: 1px solid #496DEC;
  border-top: transparent;
  border-right: transparent;
  transform: rotate(-45deg);
}
.video {
  height: 500px;
  background-color: #ddd;
  border-radius: 8px;
  overflow: hidden;
}
.video img {
  display: block;
  width: 100%;
  height: 100%;
}
.foundingteam {
  padding: 100px 0;
}
.foundingteam .title {
  text-align: center;
}
.foundingteam .title span {
  font-size: 26px;
  color: #8E8E8E;
}
.foundingteam .title h2 {
  color: #4A4A4A;
  font-size: 32px;
  margin-top: 15px;
  font-weight: 500px;
}
.foundingteam .card {
  margin-top: 76px;
}
.foundingteam .card .bg {
  position: absolute;
  top: 70px;
  right: 50px;
  width: 170px;
  z-index: 0;
}
.foundingteam .card .carditem {
  position: relative;
  width: 570px;
  height: 510px;
  box-sizing: border-box;
  padding: 45px;
  background-color: #fff;
  box-shadow: 0 20px 100px #eeeeee;
  border-radius: 5px;
}
.foundingteam .card .carditem .portrait {
  position: absolute;
  width: 140px;
  height: 140px;
  left: 20px;
  top: 30px;
  border-radius: 50%;
}
.foundingteam .card .carditem .name {
  height: 50px;
  color: #4A4A4A;
  font-size: 30px;
  margin-left: 120px;
  margin-top: 15px;
  position: relative;
  z-index: 10;
}
.foundingteam .card .carditem .posi {
  margin-left: 120px;
  height: 50px;
  color: #4A4A4A;
  font-size: 16px;
  margin-bottom: 30px;
  position: relative;
  z-index: 10;
}
.foundingteam .card .carditem p {
  font-size: 14px;
  color: #595F6F;
  line-height: 30px;
  position: relative;
  z-index: 10;
}
.foundingteam .card .left {
  float: left;
}
.foundingteam .card .right {
  float: right;
}
