footer {
  position: relative;
  padding: 100px 0;
  border-top: 1px solid #E8EAED;
}
footer .foot {
  position: relative;
  margin: 0 auto;
  width: 1200px;
}
footer .foot .grid {
  float: left;
  margin-right: 200px;
}
footer .foot .grid h3 {
  font-size: 14px;
  color: #4A4A4A;
}
footer .foot .grid li {
  font-size: 14px;
  color: #808080;
  margin-top: 15px;
}
footer .foot .addressmap {
  float: right;
  margin-right: 0;
}
footer .foot .addressmap img {
  width: 470px;
}
